body {
  margin: 0;
  background-color: #ede3d9;
  background-image: url(../img/bg-body.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
img {
  max-width: 100%;
}
.header {
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
}
.header__title {
  margin: 0;
  font-weight: 700;
  font-size: 19px;
  line-height: 1.4;
  font-family: "Maitree";
  color: #915110;
  font-style: italic;
}
.header__logo {
  display: block;
  margin: 0 auto;
  width: 140px;
}
.first {
  padding-left: 20px;
  padding-right: 20px;
}
.first__title {
  max-width: 380px;
  margin: 0 auto 14px;
  padding: 20px 15px;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.2;
  font-family: "Maitree";
  text-align: center;
  color: #ffffff;
  font-style: italic;
  background: #fff601;
  background: linear-gradient(180deg, #fff601, #c76f16);
  border-width: 2px;
  border-style: dashed;
  border-color: #ffffff;
  border-radius: 214px;
  transform: rotate(-10deg);
  text-decoration-line: underline;
}
.first__img-1 {
  display: block;
  box-sizing: border-box;
  transform: rotate(-3deg);
  width: 170px;
  margin: 0 auto;
  border: 4px solid #fff;
}
.first__img-2 {
  position: absolute;
}
